.header {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  width: 40px;
  height: 40px;
}

.stars {
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") 50% no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__reviews {
  color: #060b35;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.catalog {
  box-sizing: border-box;
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  line-height: 14px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  box-sizing: border-box;
  cursor: pointer;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card__img {
  align-self: center;
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.card__title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card:hover .card__title {
  color: #34568b;
}

.card__info {
  color: #616070;
  margin-bottom: 16px;
}

.card__rating {
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 24px;
  display: flex;
}

.card__price {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__price-title {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price-count {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.nav__list {
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav__item {
  align-items: center;
  height: 60px;
  display: flex;
}

.nav__item:first-child {
  margin-right: 0;
}

.nav__link {
  color: #060b35;
  margin-right: 20px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  transition: color .3s;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__link.is-active {
  color: #00acdc;
  position: relative;
}

.nav__link.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.button {
  box-sizing: border-box;
  justify-content: center;
  align-items: center;
  display: flex;
}

.button__link {
  text-transform: uppercase;
