  width: 100%;
  padding: 11px 70px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: background-color .3s, color .3s;
  display: block;
}

.button__link:hover {
  color: #00acdc;
  background-color: #f3f3f3;
}

* {
  margin: 0;
